.review-cart:hover img {
  transition: all ease 0.5s;
  transform: scale(1.2);
}

.review-cart .review-image {
  overflow: hidden;
  border-radius: 50%;
  height: 64px;
  width: 64px;
  margin-bottom: 10px;
}

.review-cart .review-image img {
  transition: all ease 0.5s;
  height: 64px;
  width: 64px;
  object-fit: cover;
}

.review-cart .review-detail {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 0 16px;
}

.review-cart .review-detail .post-on {
  display: flex;
  flex-direction: column;
  align-items: end;
}

.review-cart .review-detail .post-on h6 {
  color: #0C7867;
}

.team-wrapper .team-container-title {
  margin-bottom: 30px;
  text-align: center;
}

.team-wrapper .team-container-title h3 {
  margin-bottom: 8px;
}

.team-wrapper .team-cart {
  display: flex;
  flex-direction: column;
  padding: 16px;
  margin-bottom: 30px;
  border: 1px solid #cccccc6e;
  border-radius: 16px;
  align-items: center;
  justify-items: center;
}

.team-wrapper .team-cart:hover img {
  transition: all ease 0.5s;
  transform: scale(1.2);
}

.team-wrapper .team-cart .team-image {
  overflow: hidden;
  border-radius: 50%;
  height: 140px;
  width: 140px;
  margin-bottom: 10px;
}

.team-wrapper .team-cart .team-image img {
  transition: all ease 0.5s;
  height: 140px;
  width: 140px;
  object-fit: cover;
}

.team-wrapper .team-cart .team-detail {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 0 32px;
}

.team-wrapper .team-rating {
  display: flex;
}

.team-wrapper .team-rating .stars {
  margin-right: 16px;
}

.team-wrapper .team-rating .star-rating {
  font-weight: 500;
  font-size: 20px;
  line-height: 21px;
}
